













body {
  overflow: hidden;
  margin: 0;
}
h1 {
  position: fixed;
  top: 50%;
  left: 0;
  width: 100%;
  text-align: center;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-family: "Love Ya Like A Sister", cursive;
  font-size: 40px;
  color: #c70012;
  padding: 0 20px;
}
h1 span {
  font-size: 20px;
}
* {
  margin: 0;
  padding: 0;
}
body,
html {
  width: 100%;
  height: 100%;
}
ul {
  list-style: none;
}
li i {
  text-align: center;
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  display: block;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: radial-gradient(
    circle closest-side,
    rgba(84, 215, 242, 0.1) 40%,
    rgba(139, 244, 230, 0.2)
  );
}
.nav li i {
  -webkit-animation: lightAni1 10s forwards;
          animation: lightAni1 10s forwards;
}
@-webkit-keyframes lightAni1 {
0% {
    opacity: 0.5;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    opacity: 1;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
100% {
    opacity: 0;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    top: 0;
}
}
@keyframes lightAni1 {
0% {
    opacity: 0.5;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    opacity: 1;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
100% {
    opacity: 0;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    top: 0;
}
}
